@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;500;600;700&family=JetBrains+Mono:wght@400;500&display=swap');

*, *::before, *::after { box-sizing: border-box; margin: 0; padding: 0; }

body {
  background: #f5f5f7;
  min-height: 100vh;
  font-family: 'Outfit', sans-serif;
  transition: background 0.3s ease;
}

body.dark { background: #0a0a0f; }

.app {
  display: flex;
  height: 100vh;
  height: 100dvh;
  max-width: 780px;
  margin: 0 auto;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 0 0 60px rgba(0,0,0,0.08);
  transition: background 0.3s ease;
  overflow: hidden;
}

.app.dark {
  background: #111118;
  box-shadow: 0 0 60px rgba(0,0,0,0.4);
}

@supports (-webkit-touch-callout: none) {
  .app { height: -webkit-fill-available; }
}

/* ─── HEADER ─────────────────────────────────── */
.header {
  padding: 12px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #f0f0f0;
  background: #fff;
  transition: all 0.3s ease;
  flex-shrink: 0;
  min-height: 56px;
}

.app.dark .header { background: #111118; border-bottom-color: #1e1e2e; }

.header-left {
  display: flex;
  align-items: center;
  gap: 10px;
  min-width: 0;
  flex: 1;
}

.logo-icon {
  width: 34px; height: 34px;
  background: #111;
  border-radius: 10px;
  display: flex; align-items: center; justify-content: center;
  font-size: 16px;
  flex-shrink: 0;
}

.app.dark .logo-icon { background: #fff; }

.header-info { min-width: 0; }
.header-title { font-size: 15px; font-weight: 700; color: #111; letter-spacing: -0.3px; white-space: nowrap; }
.header-subtitle { font-size: 11px; color: #aaa; margin-top: 1px; white-space: nowrap; }
.app.dark .header-title { color: #fff; }

.header-actions {
  display: flex;
  align-items: center;
  gap: 6px;
  flex-shrink: 0;
}

.header-email {
  font-size: 11px;
  color: #aaa;
  font-family: 'Outfit', sans-serif;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 130px;
}

.icon-btn {
  width: 34px; height: 34px;
  border: 1.5px solid #ebebeb;
  border-radius: 9px;
  background: #fff;
  cursor: pointer;
  display: flex; align-items: center; justify-content: center;
  font-size: 15px;
  transition: all 0.15s ease;
  color: #555;
  flex-shrink: 0;
}

.icon-btn:hover { border-color: #111; background: #f5f5f5; color: #111; }
.app.dark .icon-btn { background: #1a1a24; border-color: #2a2a35; color: #aaa; }
.app.dark .icon-btn:hover { border-color: #fff; background: #222; color: #fff; }

.status-badge {
  display: flex; align-items: center; gap: 6px;
  font-size: 11px; color: #888;
  background: #f7f7f7;
  padding: 5px 10px;
  border-radius: 100px;
  border: 1px solid #eee;
  font-weight: 500;
  white-space: nowrap;
}

.app.dark .status-badge { background: #1a1a24; border-color: #2a2a35; color: #666; }
.status-dot { width: 7px; height: 7px; background: #22c55e; border-radius: 50%; animation: statusPulse 2s infinite; }

@keyframes statusPulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

/* ─── CHAT AREA ──────────────────────────────── */
.chat-area {
  flex: 1;
  overflow-y: auto;
  padding: 20px 20px 12px;
  display: flex;
  flex-direction: column;
  gap: 14px;
  background: #fafafa;
  scrollbar-width: thin;
  scrollbar-color: #e5e5e5 transparent;
  transition: background 0.3s ease;
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: contain;
}

.app.dark .chat-area { background: #0d0d14; scrollbar-color: #2a2a35 transparent; }
.chat-area::-webkit-scrollbar { width: 3px; }
.chat-area::-webkit-scrollbar-thumb { background: #e5e5e5; border-radius: 4px; }
.app.dark .chat-area::-webkit-scrollbar-thumb { background: #2a2a35; }

/* ─── EMPTY STATE ────────────────────────────── */
.empty-state {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px;
  padding: 32px 20px;
  animation: fadeIn 0.5s ease;
}

.empty-icon {
  width: 52px; height: 52px;
  background: #111;
  border-radius: 16px;
  display: flex; align-items: center; justify-content: center;
  font-size: 24px;
}

.app.dark .empty-icon { background: #fff; }

.empty-title { font-size: 20px; font-weight: 700; color: #111; letter-spacing: -0.4px; text-align: center; }
.app.dark .empty-title { color: #fff; }
.empty-subtitle { font-size: 13.5px; color: #999; text-align: center; line-height: 1.6; max-width: 280px; }

.suggestions { display: flex; flex-wrap: wrap; gap: 8px; justify-content: center; margin-top: 4px; }

.suggestion-chip {
  padding: 7px 14px;
  border: 1.5px solid #e8e8e8;
  border-radius: 100px;
  font-size: 12.5px;
  color: #555;
  cursor: pointer;
  background: #fff;
  transition: all 0.15s ease;
  font-family: 'Outfit', sans-serif;
  font-weight: 500;
}

.suggestion-chip:hover { border-color: #111; color: #111; background: #f5f5f5; }
.app.dark .suggestion-chip { background: #1a1a24; border-color: #2a2a35; color: #aaa; }
.app.dark .suggestion-chip:hover { border-color: #fff; color: #fff; }

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to   { opacity: 1; transform: translateY(0); }
}

/* ─── MESSAGES ───────────────────────────────── */
.message-row { display: flex; gap: 8px; animation: fadeIn 0.25s ease; }
.message-row.user { flex-direction: row-reverse; }

.avatar {
  width: 28px; height: 28px;
  border-radius: 8px;
  display: flex; align-items: center; justify-content: center;
  font-size: 12px;
  flex-shrink: 0;
  margin-top: 2px;
}

.avatar.ai   { background: #111; }
.avatar.user { background: #ebebeb; }
.app.dark .avatar.ai   { background: #fff; }
.app.dark .avatar.user { background: #2a2a35; }

.message-content { max-width: 75%; display: flex; flex-direction: column; gap: 4px; }
.message-row.user .message-content { align-items: flex-end; }

.message-bubble {
  padding: 10px 14px;
  border-radius: 14px;
  font-size: 14px;
  line-height: 1.65;
  font-weight: 400;
  word-break: break-word;
  overflow-wrap: anywhere;
}

.message-bubble.ai {
  background: #fff;
  border: 1.5px solid #efefef;
  color: #222;
  border-bottom-left-radius: 4px;
}

.app.dark .message-bubble.ai { background: #1a1a24; border-color: #2a2a35; color: #ddd; }

.message-bubble.user {
  background: #111;
  color: #fff;
  border-bottom-right-radius: 4px;
}

.app.dark .message-bubble.user { background: #4f46e5; }

.message-bubble.image-bubble {
  padding: 10px;
  background: #fff;
  border: 1.5px solid #efefef;
  border-bottom-left-radius: 4px;
}

.app.dark .message-bubble.image-bubble { background: #1a1a24; border-color: #2a2a35; }

/* MARKDOWN */
.message-bubble p { margin-bottom: 6px; }
.message-bubble p:last-child { margin-bottom: 0; }
.message-bubble code {
  background: rgba(0,0,0,0.07);
  padding: 1px 5px;
  border-radius: 4px;
  font-family: 'JetBrains Mono', monospace;
  font-size: 12px;
}
.app.dark .message-bubble code { background: rgba(255,255,255,0.1); }
.message-bubble pre {
  background: #f0f0f0;
  padding: 10px 12px;
  border-radius: 8px;
  overflow-x: auto;
  margin: 6px 0;
  font-family: 'JetBrains Mono', monospace;
  font-size: 12px;
}
.app.dark .message-bubble pre { background: #0a0a12; }
.message-bubble strong { font-weight: 600; }
.message-bubble ul, .message-bubble ol { padding-left: 18px; margin: 4px 0; }
.message-bubble li { margin-bottom: 2px; }
.message-bubble h1, .message-bubble h2, .message-bubble h3 { font-weight: 700; margin-bottom: 6px; }

/* MESSAGE ACTIONS */
.message-actions { display: none; gap: 4px; margin-top: 2px; }
.message-row:hover .message-actions { display: flex; }

.action-btn {
  padding: 3px 8px;
  border: 1px solid #e8e8e8;
  border-radius: 6px;
  background: #fff;
  font-size: 11px;
  color: #888;
  cursor: pointer;
  transition: all 0.15s ease;
  font-family: 'Outfit', sans-serif;
}

.action-btn:hover { border-color: #111; color: #111; background: #f5f5f5; }
.app.dark .action-btn { background: #1a1a24; border-color: #2a2a35; color: #666; }
.app.dark .action-btn:hover { border-color: #fff; color: #fff; }

/* IMAGE */
.image-caption { font-size: 12.5px; color: #888; margin-bottom: 6px; }
.generated-image { width: 100%; max-width: 320px; border-radius: 10px; display: block; }

.image-download {
  display: inline-block;
  margin-top: 8px;
  font-size: 12px;
  color: #111;
  text-decoration: none;
  border: 1px solid #e0e0e0;
  padding: 4px 12px;
  border-radius: 100px;
  font-weight: 500;
  transition: all 0.15s ease;
}

.image-download:hover { background: #111; color: #fff; border-color: #111; }
.app.dark .image-download { color: #aaa; border-color: #2a2a35; }
.app.dark .image-download:hover { background: #fff; color: #111; }

/* IMAGE PROGRESS */
.image-progress {
  padding: 14px 16px;
  background: #fff;
  border: 1.5px solid #efefef;
  border-radius: 14px;
  border-bottom-left-radius: 4px;
  min-width: 200px;
}

.app.dark .image-progress { background: #1a1a24; border-color: #2a2a35; }
.progress-label { font-size: 12.5px; color: #888; margin-bottom: 8px; }
.app.dark .progress-label { color: #666; }
.progress-bar-track { height: 4px; background: #f0f0f0; border-radius: 4px; overflow: hidden; }
.app.dark .progress-bar-track { background: #2a2a35; }

.progress-bar-fill {
  height: 100%;
  background: #111;
  border-radius: 4px;
  animation: progress 8s ease-in-out forwards;
}

.app.dark .progress-bar-fill { background: #4f46e5; }

@keyframes progress {
  0%   { width: 0%; }
  20%  { width: 30%; }
  50%  { width: 60%; }
  80%  { width: 85%; }
  100% { width: 95%; }
}

/* MESSAGE TIME */
.message-time { font-size: 10px; color: #bbb; padding: 0 2px; }
.app.dark .message-time { color: #444; }

/* TYPING */
.typing-bubble {
  padding: 12px 16px;
  background: #fff;
  border: 1.5px solid #efefef;
  border-radius: 14px;
  border-bottom-left-radius: 4px;
  display: flex; gap: 5px; align-items: center;
}

.app.dark .typing-bubble { background: #1a1a24; border-color: #2a2a35; }

.typing-dot {
  width: 6px; height: 6px;
  background: #ccc;
  border-radius: 50%;
  animation: bounce 1.2s infinite;
}

.typing-dot:nth-child(2) { animation-delay: 0.2s; }
.typing-dot:nth-child(3) { animation-delay: 0.4s; }

@keyframes bounce {
  0%, 60%, 100% { transform: translateY(0); }
  30%            { transform: translateY(-5px); }
}

/* ─── SIDEBAR ────────────────────────────────── */
.sidebar {
  position: fixed;
  left: 0; top: 0;
  height: 100vh; height: 100dvh;
  width: 270px;
  background: #fff;
  border-right: 1px solid #f0f0f0;
  z-index: 200;
  display: flex; flex-direction: column;
  transform: translateX(-100%);
  transition: transform 0.28s cubic-bezier(0.4,0,0.2,1);
  box-shadow: 4px 0 24px rgba(0,0,0,0.08);
}

.sidebar.open { transform: translateX(0); }
.app.dark .sidebar { background: #111118; border-right-color: #1e1e2e; }

.sidebar-header {
  padding: 16px;
  border-bottom: 1px solid #f0f0f0;
  display: flex; justify-content: space-between; align-items: center;
  flex-shrink: 0;
}

.app.dark .sidebar-header { border-bottom-color: #1e1e2e; }
.sidebar-title { font-size: 14px; font-weight: 700; color: #111; }
.app.dark .sidebar-title { color: #fff; }

.sidebar-body { flex: 1; overflow-y: auto; padding: 10px; display: flex; flex-direction: column; gap: 4px; -webkit-overflow-scrolling: touch; }

.history-item {
  padding: 10px 12px;
  border-radius: 10px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 0.15s ease;
}

.history-item:hover { background: #f5f5f5; border-color: #ebebeb; }
.app.dark .history-item:hover { background: #1a1a24; border-color: #2a2a35; }

.history-item-title { font-size: 13px; font-weight: 600; color: #222; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }
.app.dark .history-item-title { color: #ddd; }
.history-item-date { font-size: 11px; color: #aaa; margin-top: 2px; }

.new-chat-btn {
  margin: 12px 12px 4px;
  padding: 10px;
  background: #111;
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-family: 'Outfit', sans-serif;
  font-size: 13px;
  font-weight: 600;
  transition: all 0.15s ease;
  flex-shrink: 0;
}

.new-chat-btn:hover { background: #333; }
.app.dark .new-chat-btn { background: #fff; color: #111; }
.app.dark .new-chat-btn:hover { background: #eee; }

.empty-history { font-size: 13px; color: #bbb; text-align: center; padding: 24px 16px; }

/* MOBILE LOGOUT in sidebar */
.mobile-logout {
  margin: 8px 12px 16px;
  padding: 11px;
  background: #fff5f5;
  color: #ef4444;
  border: 1.5px solid #fecaca;
  border-radius: 10px;
  cursor: pointer;
  font-family: 'Outfit', sans-serif;
  font-size: 13px;
  font-weight: 600;
  transition: all 0.15s ease;
  flex-shrink: 0;
  text-align: center;
}

.mobile-logout:hover { background: #fee2e2; }
.app.dark .mobile-logout { background: #1a0a0a; border-color: #7f1d1d; color: #f87171; }
.app.dark .mobile-logout:hover { background: #2a0f0f; }

/* ─── VOICE ──────────────────────────────────── */
.voice-btn {
  width: 34px; height: 34px;
  border: 1.5px solid #ebebeb;
  border-radius: 9px;
  background: #fff;
  cursor: pointer;
  display: flex; align-items: center; justify-content: center;
  font-size: 15px;
  transition: all 0.15s ease;
  flex-shrink: 0;
}

.voice-btn.listening { background: #fee2e2; border-color: #f87171; animation: pulse 1s infinite; }
.voice-btn:hover { border-color: #111; }
.app.dark .voice-btn { background: #1a1a24; border-color: #2a2a35; }
.app.dark .voice-btn.listening { background: #3f0000; border-color: #f87171; }

@keyframes pulse {
  0%, 100% { box-shadow: 0 0 0 0 rgba(248,113,113,0.4); }
  50%       { box-shadow: 0 0 0 6px rgba(248,113,113,0); }
}

/* ─── INPUT AREA ─────────────────────────────── */
.input-area {
  padding: 12px 16px 16px;
  background: #fff;
  border-top: 1px solid #f0f0f0;
  transition: background 0.3s ease;
  flex-shrink: 0;
}

.app.dark .input-area { background: #111118; border-top-color: #1e1e2e; }

.input-container {
  display: flex;
  align-items: flex-end;
  gap: 8px;
  background: #f7f7f7;
  border: 1.5px solid #ebebeb;
  border-radius: 14px;
  padding: 8px 8px 8px 14px;
  transition: border-color 0.15s ease, background 0.15s ease;
}

.input-container:focus-within { border-color: #111; background: #fff; }
.app.dark .input-container { background: #1a1a24; border-color: #2a2a35; }
.app.dark .input-container:focus-within { border-color: #4f46e5; background: #1a1a24; }

.input-field {
  flex: 1;
  background: transparent;
  border: none;
  outline: none;
  color: #222;
  font-family: 'Outfit', sans-serif;
  font-size: 14px;
  line-height: 1.5;
  resize: none;
  max-height: 120px;
  scrollbar-width: none;
  min-width: 0;
}

.app.dark .input-field { color: #ddd; }
.input-field::placeholder { color: #bbb; }
.input-field::-webkit-scrollbar { display: none; }

.send-btn {
  width: 34px; height: 34px;
  background: #111;
  border: none;
  border-radius: 9px;
  cursor: pointer;
  display: flex; align-items: center; justify-content: center;
  flex-shrink: 0;
  transition: all 0.15s ease;
  color: white;
  font-size: 16px;
}

.send-btn:hover:not(:disabled) { background: #333; transform: translateY(-1px); }
.send-btn:disabled { opacity: 0.3; cursor: not-allowed; }
.app.dark .send-btn { background: #4f46e5; }
.app.dark .send-btn:hover:not(:disabled) { background: #4338ca; }

.input-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 6px;
  padding: 0 2px;
}

.input-hint  { font-size: 11px; color: #ccc; }
.app.dark .input-hint { color: #333; }
.model-tag   { font-size: 10.5px; color: #bbb; font-family: 'JetBrains Mono', monospace; }
.app.dark .model-tag { color: #333; }

/* ─── TOAST ──────────────────────────────────── */
.toast {
  position: fixed;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%) translateY(60px);
  background: #111;
  color: #fff;
  padding: 8px 18px;
  border-radius: 100px;
  font-size: 13px;
  font-weight: 500;
  z-index: 999;
  opacity: 0;
  transition: all 0.3s ease;
  pointer-events: none;
  white-space: nowrap;
}

.toast.show { transform: translateX(-50%) translateY(0); opacity: 1; }
.app.dark .toast { background: #fff; color: #111; }

/* ─── OVERLAY ────────────────────────────────── */
.overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.4);
  z-index: 199;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.28s ease;
}

.overlay.show { opacity: 1; pointer-events: all; }


/* ═══════════════════════════════════════════════
   MOBILE — max-width: 768px
   ═══════════════════════════════════════════════ */
@media (max-width: 768px) {

  /* Full screen */
  .app { max-width: 100%; box-shadow: none; }

  /* ── Header ── */
  .header { padding: 8px 12px; min-height: 52px; }
  .header-left { gap: 8px; }
  .header-title { font-size: 14px; }
  .header-subtitle { display: none; }
  .header-email { display: none; }
  .status-badge { display: none; }
  .header-actions { gap: 4px; }

  .logo-icon { width: 30px; height: 30px; font-size: 14px; border-radius: 8px; }
  .icon-btn  { width: 30px; height: 30px; font-size: 13px; border-radius: 8px; }

  /* ── Chat ── */
  .chat-area { padding: 12px 12px 8px; gap: 10px; }
  .message-content { max-width: 86%; }
  .message-bubble { font-size: 13.5px; padding: 9px 12px; }
  .avatar { width: 26px; height: 26px; font-size: 11px; border-radius: 7px; }
  .message-time { font-size: 10px; }

  /* Always show copy button on mobile (no hover) */
  .message-actions { display: flex; }

  /* ── Empty state ── */
  .empty-state { padding: 16px 14px; gap: 10px; }
  .empty-icon  { width: 44px; height: 44px; font-size: 20px; border-radius: 12px; }
  .empty-title { font-size: 16px; }
  .empty-subtitle { font-size: 12.5px; max-width: 260px; }
  .suggestions { gap: 6px; margin-top: 2px; }
  .suggestion-chip { padding: 6px 11px; font-size: 12px; }

  /* ── Input ── */
  .input-area {
    padding: 8px 12px;
    padding-bottom: max(12px, env(safe-area-inset-bottom));
  }

  .input-container { padding: 7px 7px 7px 12px; border-radius: 12px; gap: 6px; }
  .input-field { font-size: 16px; } /* stops iOS zoom */
  .send-btn  { width: 32px; height: 32px; font-size: 14px; border-radius: 8px; }
  .voice-btn { width: 32px; height: 32px; font-size: 13px; border-radius: 8px; }
  .input-footer { margin-top: 4px; }
  .input-hint { font-size: 10px; }
  .model-tag  { font-size: 10px; }

  /* ── Sidebar ── */
  .sidebar { width: 78%; max-width: 300px; }

  /* ── Images ── */
  .generated-image { max-width: 100%; }
  .image-progress { min-width: 150px; }

  /* ── Toast ── */
  .toast { bottom: 70px; font-size: 12px; padding: 7px 14px; }

  /* ── Typing ── */
  .typing-bubble { padding: 10px 14px; }
}

/* Extra small (iPhone SE) */
@media (max-width: 375px) {
  .header-title { font-size: 13px; }
  .icon-btn { width: 28px; height: 28px; font-size: 12px; }
  .message-content { max-width: 90%; }
  .empty-title { font-size: 15px; }
  .suggestion-chip { font-size: 11px; padding: 5px 10px; }
  .message-bubble { font-size: 13px; padding: 8px 11px; }
}